body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 20px;
  background-color: #f4f4f4;
}

input[type='text'] {
  display: inline-block;
  margin-right: 10px;
  padding: 5px;
  border-radius: 5px;
  border: none;
}

.employee {
  border-radius: 5px;
  box-shadow: 0px 0px 5px rgba(0,0,0,0.1);
  padding: 10px;
  margin-top: 10px;
  background-color: white;
}

.employee p {
  margin: 0;
}

.employee p + p {
  margin-top: 5px;
}

.skills,
.projects,
.team {
  margin-left: 20px;
}
